* {
	margin: 0;
	padding: 0; }

body {
	background: #bfbfbf;
	display: flex;
	justify-content: center;
	min-width: 1100px;

	main {
		background: #fff;
		width: 980px;

		form {
			position: relative;
			z-index: 0;
			margin: 139px 39px 27px 34px;

			fieldset:nth-of-type(1) {
				height: 489px;
				padding: 0 0 21px 18px;
				background: linear-gradient(0deg, rgb(238,238,238) 0%, rgb(255,255,255) 50%, rgb(247,247,247) 74%, rgb(238,238,238) 100%);
				border-radius: 5px;
				border: none;
				font-family: PTSans, sans-serif;
				font-size: 11px;
				font-weight: 100;

				&::before, p:nth-of-type(5), p:nth-of-type(6), img {
					position: absolute; }

				&::before {
					content: " ";
					top: 12px;
					left: -1px;
					width: 909px;
					height: 499px;
					background: linear-gradient(#eee, #ccc);
					border-radius: 5px;
					z-index: -1; }

				legend {
					margin-left: -2px;
					padding: 4px 57px 6px 48px;
					font-size: 12px;
					border-radius: 28px;
					color: #fff;
					background: linear-gradient(rgb(250,166,5), rgb(208,78,0)); }

				span {
					text-shadow: 0px -1px 1px #555; }

				p {
					text-shadow: -1px 0px 1px #a8a8a8;
					color: #585858; }

				p:nth-of-type(1) {
					margin: 18px 0 7px; }

				p:nth-of-type(2) {
					margin: 19px 0 4px; }

				p:nth-of-type(3) {
					margin: 21px 0 5px; }

				p:nth-of-type(4) {
					margin: 21px 0 4px; }

				p:nth-of-type(5), p:nth-of-type(6) {
					color: rgb(251, 106, 10);
					text-shadow: none; }

				p:nth-of-type(5) {
					top: 45px;
					left: 564px;
					text-transform: uppercase;
					font-size: 24px;
					font-stretch: condensed;
					background: linear-gradient(42deg, rgb(251, 106, 10), rgb(255, 193, 3));
					-webkit-background-clip: text;
					-webkit-text-fill-color: transparent; }

				p:nth-of-type(6) {
					top: 69px;
					left: 566px;
					font-size: 14px; }

				input, textarea {
					border: none; }

				input {
					width: 311px;
					height: 20px;
					box-shadow: inset 0 -1px 1px #e5e5e5, inset 0 1px 1px #9b9b9b, inset 1px 0 3px #d4d4d4, inset -1px 0 3px #d4d4d4; }

				textarea {
					width: 424px;
					height: 131px;
					resize: none;
					box-shadow: inset 1px 1px 3px #d1d1d1; }

				img:nth-of-type(1) {
					top: 172px;
					left: 516px; }

				img:nth-of-type(2) {
					top: 154px;
					left: 696px; }

				img:nth-of-type(3) {
					top: 126px;
					left: 616px; } }

			fieldset:nth-of-type(2) {
				margin-top: 27px;
				padding: 0 0 21px 18px;
				background: linear-gradient(0deg, rgb(238,238,238) 0%, rgb(255,255,255) 50%, rgb(247,247,247) 74%, rgb(238,238,238) 100%);
				border-radius: 5px;
				border: none;
				font-family: PTSans, sans-serif;
				font-size: 11px;
				font-weight: 100;

				&::before {
					content: " ";
					position: absolute;
					top: 550px;
					left: 500px;
					width: 100px;
					height: 100px;
					background: linear-gradient(#eee, #ccc);
					border-radius: 5px;
					z-index: -1; }

				legend {
					margin-left: -2px;
					padding: 4px 57px 6px 48px;
					font-size: 12px;
					border-radius: 28px;
					color: #fff;
					background: linear-gradient(rgb(172,220,4), rgb(49,137,1)); }

				span {
					text-shadow: 0px -1px 1px #555; }

				p {
					text-shadow: -1px 0px 1px #a8a8a8;
					color: #585858; } } } } }















































